#picContainer {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-row-gap: 1rem;
    margin-top: 1rem;
    max-width: 100vw;
}

.img {
    width: 10rem;
    height: 10rem;
    border: 1px solid black;
}

.picBox {
    height: 12rem;
    width: 12rem;
}

@media (max-width: 600px) {
    #picContainer {
        display: grid;
        grid-template-columns: 1fr;
        grid-row-gap: 1rem;
    }
    .img {
        width: 100%;
        height: 15rem;
        border: 1px solid black;
    }
    .picBox {
        height: 18rem;
        width: 100vw;
    }
}